import React from 'react';
import { Banner, Content } from '@sections/acknowledgements';
import { SEO, Wrapper } from '@components';
import { graphql, PageProps, useStaticQuery } from 'gatsby';

const THUMBNAIL_QUERY = graphql`
  {
    image: file(relativePath: { eq: "photos/thumbnail.png" }) {
      childImageSharp {
        resize(width: 1200) {
          src
          height
          width
        }
      }
    }
  }
`;
const Acknowledgements = (pageProps: PageProps) => {
  const data = useStaticQuery(THUMBNAIL_QUERY);
  const { image } = data;

  return (
    <>
      <SEO
        title="Acknowledgements"
        pathname={pageProps.location.pathname}
        image={image?.childImageSharp?.resize}
      />

      <Wrapper>
        <Banner />
        <Content />
      </Wrapper>
    </>
  );
};

export default Acknowledgements;
